import { useBemCN, useBreakpoints } from "shared"
import './style.scss'

export const Results = () => {
  const resultsBlock = useBemCN('results')
  const { isMobile } = useBreakpoints()
  return (
    <div
      className={resultsBlock()}
    >
      <h3
        className={resultsBlock('title')}
      >
           :
      </h3>
      <p
        className={resultsBlock('money')}
      >
        124 000 
      </p>
      <table
        className={resultsBlock('table')}
      >
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th>{isMobile ? '  ' : '  '}</th>
            <th>{isMobile ? '  ' : '  '}</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td> </td>
            <td>1 </td>
            <td>1 500 </td>
            <td>10 500 </td>
          </tr>
          <tr>
            <td> 1</td>
            <td>2 </td>
            <td>2 300 </td>
            <td>24 300 </td>
          </tr>
          <tr>
            <td> 2</td>
            <td>1 </td>
            <td>1 700 </td>
            <td>16 700 </td>
          </tr>
          <tr>
            <td> 3</td>
            <td>3 </td>
            <td>3 900 </td>
            <td>37 900 </td>
          </tr>
          <tr>
            <td> 4</td>
            <td>2 </td>
            <td>4 500 </td>
            <td>48 500 </td>
          </tr>
          <tr>
            <td> </td>
            <td>8 </td>
            <td>100 000 </td>
            <td>100 000 </td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td
              className={resultsBlock('table-result-text')}
            >
              
            </td>
            <td>8.5 </td>
            <td
              className={resultsBlock('table-result-price')}
            >113 900 </td>
            <td>237 900 </td>
          </tr>
        </tfoot>
      </table>
      <p
        className={resultsBlock('text')}
      >
        *      -.
      </p>
    </div>
  )
}